<template>
    <div class="false-user-list">
        <div class="password-manager">
            <div class="false-user-header">
                <span class="false-user-title">修改登录密码</span>
                <p class="false-user-poptip">
                    <span>注：修改密码成功后，将弹出后台重新登录</span>
                </p>
            </div>
            <div class="account-password">
                <div class="password-item">
                    <label>原密码</label>
                    <input type="password" v-model="oldPassword" placeholder="请输入原登录密码"/>
                </div>
                <div class="password-item">
                    <label>新密码</label>
                    <input type="password" v-model="newPassword" placeholder="输入新的登录密码"/>
                </div>
                <div class="password-item">
                    <label>确认密码</label>
                    <input type="password" v-model="repeatPassword" placeholder="再次输入新的登录密码"/>
                </div>
                <div class="update-password">
                    <span @click="handleUpdatePassword">确定</span>
                </div>
            </div>
        </div>
        <div style="margin: 20px 0">
            <againPassword></againPassword>
        </div>
    </div>
</template>

<script>
import againPassword from '../againPassword/index.vue';
export default {
    components: {
        againPassword
    },
    data() {
        return {
            oldPassword: '',
            newPassword: '',
            repeatPassword: '',
            isLoading: false
        }
    },
    methods: {
        handleUpdatePassword() {
            let that = this;

            if (!that.oldPassword) {
                that.$Message.error('请输入原登录密码');
                return;
            }

            if (!that.newPassword) {
                that.$Message.error('请输入新的登录密码');
                return;
            }

            if (!that.repeatPassword) {
                that.$Message.error('请输入确认登录密码');
                return;
            }

            if (that.repeatPassword !== that.newPassword) {
                that.$Message.error('新的登录密码与确认登录密码不一致');
                return;
            }

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Merchant/UpdatePassword',
                    method: 'post',
                    params: {
                        oldPwd: that.oldPassword,
                        newPwd: that.newPassword
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.handleExit();
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                })
            }
        },
        handleExit() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;
                that.axios({
                    url: '/api/Merchant/MerchantExit',
                    method: 'post'
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$router.push({name: 'login'});
                    }
                })
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.false-user-list {

    .password-manager {
        padding: 0 10px;
        border-radius: 5px;
        background-color: rgb(242, 242, 242);
        border: 1px solid rgb(215, 215, 215);
        box-sizing: border-box;
    }
    .false-user-header {
        display: flex;
        justify-content: space-between;

        padding: 20px 20px 13px 20px;
        border-bottom: solid 1px #e4e4e4;

        .false-user-title {
            position: relative;

            padding-left: 11px;

            height: 20px;
            line-height: 20px;

            color: rgb(51, 51, 51);
            font-size: 14px;
            font-weight: 700;
        }

        .false-user-title:before {
            content: '';

            position: absolute;
            top: 2px;
            left: 0;

            width: 5px;
            height: 15px;

            background-color: #399;
        }

        .false-user-poptip {
            font-size: 14px;
            font-weight: 400;
            color: rgb(102, 102, 102);
            text-align: left;
        }
    }

    .account-password {
        display: flex;
        flex-direction: column;
        align-items: center;

        padding: 20px 0;

        .password-item {
            height: 70px;
            line-height: 70px;

            label {
                display: inline-block;
                width: 90px;

                text-align: center;
                color: #000;
            }

            input {
                padding-left: 8px;
                width: 310px;
                height: 32px;
                line-height: 32px;
                border-radius: 4px;
                border: 1px solid #dcdee2;
            }
        }

        .update-password {
            height: 70px;
            line-height: 70px;

            & > span {
                display: inline-block;
                width: 143px;
                height: 35px;
                line-height: 35px;
                background-color: #00e14a;
                border-radius: 4px;
                border: solid 1px #336699;
                color: #fff;
                text-align: center;
                cursor: pointer;
            }
        }
    }
}
</style>