<template>
    <div :class="['switch', size, open ? size + '-open' : size + '-close']" @click="handleSwitch">
        <div class="switch-item"></div>
    </div>
</template>

<script>
export default {
    props: {
        open: {
            type: Boolean,
            default: false
        },
        size: {
            type: String,
            default: 'small'
        }
    },
    methods: {
        handleSwitch() {
            let that = this;

            that.$emit('switch');
        }
    }
}
</script>

<style lang="scss">
.switch {
    display: inline-block;

    position: relative;
    vertical-align: middle;

    cursor: pointer;

    .switch-item {
        position: absolute;
        top: 0;
        bottom: 0;

        margin: auto;
    }
}

.small {
    width: 40px;
    height: 25px;

    border-top-left-radius: 12.5px 50%;
    border-top-right-radius: 12.5px 50%;
    border-bottom-right-radius: 12.5px 50%;
    border-bottom-left-radius: 12.5px 50%;

    .switch-item {
        width: 23px;
        height: 23px;

        border-radius: 11.5px;
        background-color: #fff;
    }
}

.big {
    width: 80px;
    height: 30px;

    border-top-left-radius: 15px 50%;
    border-top-right-radius: 15px 50%;
    border-bottom-right-radius: 15px 50%;
    border-bottom-left-radius: 15px 50%;

    .switch-item {
        width: 28px;
        height: 28px;

        border-radius: 14px;
        background-color: #fff;
    }
}

.small-close {
    background-color: rgb(221, 221, 221);

    .switch-item {
        right: 0;
        left: -30%;
    }
}

.small-open {
    background-color: rgb(74, 214, 98);

    .switch-item {
        right: -35%;
        left: 0;
    }
}

.big-close {
    background-color: rgb(221, 221, 221);

    .switch-item {
        right: 0;
        left: -60%;
    }
}

.big-open {
    background-color: rgb(74, 214, 98);
    .switch-item {
        right: -61%;
        left: 0;
    }
}
</style>